import React, {useEffect, useState} from 'react';
// setState쓰려면 useState 써야함
import axios from 'axios';
import NewsItem from '../components/NewsItem'
import styled from 'styled-components';
import { FaBeer } from 'react-icons/fa';

const NewsSectionBlock = styled.div `
    .row {
        padding-top: 80px;
        display: flex;
        flex-wrap: wrap;
        div {
            width: 50%;
        }
    }

`;
const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    // const getNews = async()=>{
    //     // const response = await axios.get('api주소')
    //     const response = await axios.get('http://newsapi.org/v2/top-headlines?country=kr&apiKey=caebc5ab76574e0d830590f5910960c2');
    //     setLoading(true);
    //     // setLoading(false);
    //     setArticles(response.data.articles);
    // }

    const getNews = async ()=>{
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=caebc5ab76574e0d830590f5910960c2`);
        // const response = await axios.get('api주소')
        console.log(response);
        setArticles(response.data.articles);
        setLoading(true);
    }
    useEffect(
        ()=>{
            getNews()
        }
    )

    if(!loading){
        return (
            <NewsSectionBlock>
                <div style={{background: "lightgray", height: "800px", display: 'flex', justifyContent:'center', alignItems:'center'}}>
                    <FaBeer style={{fontSize: '70px'}}/>
                </div>
            </NewsSectionBlock>
            )
    } else {
        if(!articles){
            // return null;
            return <NewsSectionBlock>비었음</NewsSectionBlock>;
        } else {
            return (
                <NewsSectionBlock>
                    <div className='row' >
                        {
                            articles.map( article => <NewsItem key={article.url} article={article} />)
                        }
                    </div>
                </NewsSectionBlock>
            );
        }
    }
};

export default NewsSection;